.header-name {
  color: #363636;
  font-family: "Poppins";
}
.header-role {
  overflow: hidden;
  color: #363636;
  font-family: "Poppins";
}
.header-action {
  overflow: hidden;
}
#user-card {
  padding: 40px;
  margin: 40px;
}
.user-btns {
  width: 200px;
  height: 49px;
}
.user-btns-span {
  display: flex;
  justify-content: space-between;
  margin-right: 10px;
}
.user-delete {
  border: 1px;
  border-radius: 20px;
  background-color: white;
  width: 300px;
  margin: auto;
  margin-top: 230px;
  justify-content: center;
  align-items: center;
  text-align: center;
  outline: none;
}
.user-button-delete {
  border-bottom-left-radius: 20px;
  border: 1px solid;
  border-color: lightGrey;
  text-transform: none;
  width: 149px;
  border-radius: 10px;
}
.user-button-delete:hover {
  color: grey;
}
.user-delete p {
  padding-top: 20px;
  padding-bottom: 20px;
}
.pagination {
  margin: 20px 25px 5px auto;
  display: flex;
  list-style: none;
  outline: none;
}

.user-table .ReactTable {
  margin-left: 150px;
  width: 100px;
}

.pagination > .active > a {
  background-color: #04724d;
  border-color: #121732;
  color: #ffffff;
}

.pagination > li {
  margin: 7px 10px;
}

.pagination > li > a {
  padding: 5px 12px;
  border-radius: 50%;
  outline: none;
  cursor: pointer;
}

.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  outline: none;
  color: #ffffff !important;
}

.pagination > li > a,
.pagination > li > span {
  color: #0e0e0e;
}

.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-radius: unset;
}

.pagination-bottom {
  display: flex;
  justify-content: center;
}

.user-table .ReactTable .rt-thead .rt-tr {
  border-radius: 4px !important;
  background: #ffffff;
  /* color: #04724d; */
  font-size: 18px;
  font-weight: bolder;
  text-align: center;
  padding-top: 5px;
}

.user-table .ReactTable .rt-thead.-header {
  box-shadow: none;
  text-align: center !important;
}

.ReactTable .rt-tbody {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  border-right: none !important;
}

.user-table .ReactTable .rt-thead .rt-th,
.user-table .ReactTable .rt-thead .rt-td {
  text-align: center !important;
  padding-top: 25px;
}

.user-table .ReactTable .rt-tbody .rt-td {
  border-right: none !important;
  text-align: center !important;
  padding-top: 25px;
}

.user-table .ReactTable .rt-tbody .rt-tr-group {
  border: 2px solid #36363659;
  border-radius: 15px !important;
  text-align: center !important;
}

.user-table .ReactTable .rt-th,
.user-table .ReactTable .rt-td {
  text-align: center !important;
}

.user-table .ReactTable .rt-tbody .rt-tr-group:last-child {
  border: 2px solid #36363659 !important;
  border-radius: 4px !important;
  text-align: center !important;
}

.user-btn-pencil {
  background-color: #ffffff !important;
  color: black !important;
  width: 40px !important;
  padding-bottom: 15px !important;
  font-size: 5px;
}

.user-btn-rounded {
  background-color: #ffffff !important;
  color: black !important;
  width: 40px !important;
  padding-bottom: 10px !important;
  font-size: 5px;
  margin-left: 40px !important;
}

.user-button-user {
  margin-top: -16px !important;
}

.user-btn-3 {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 40px !important;
}

.user-btn-4 {
  margin-left: 10px !important;
}

.user-table .ReactTable .rt-tbody .rt-td :nth-child(1) {
  text-align: center !important;
  padding-top: 10px !important;
}

.user-table .ReactTable .rt-tbody .rt-td :last-child {
  border-right: none !important;
  text-align: center !important;
  padding-bottom: 20px;
}

.user-table .rt-tr {
  height: 70px !important;
}

.button-div {
  display: flex;
  justify-content: flex-end;
  margin-top: 34px;
}

.save {
  color: #ffffff;
  background-color: #04724d;
  width: 175px;
  height: 52px;
  box-shadow: 0px 4px 8px #2c273814;
  opacity: 1;
}

.cancel {
  width: 175px !important;
  height: 52px !important;
  box-shadow: 0px 4px 8px #2c273814 !important;
  border: 1px solid #04724d !important;
  opacity: 1;
  background-color: #04724d;
  color: #0e0e0e;
}

.modal-middle-div {
  display: flex;
  margin-top: 30px;
}

.text-field .MuiInputBase-input {
  width: 254px !important;
  margin: 0px 10px !important;
}

.modal-upper-div {
  display: flex;
}

.outer-div {
  margin: 0px 5px;
}

.role {
  width: 233px !important;
  margin: 14px;
}

.ynbtn {
  border-bottom-right-radius: 20 !important;
  border-color: lightgrey !important;
  text-transform: none !important;
  width: 149;
  border-radius: 10 !important;
}

.ynbtn:hover {
  color: grey !important;
}
@media only screen and (min-width: 768px) and (max-width: 1024px) {
  .user-table .ReactTable {
    width: 540px;
    margin-left: 5px;
  }
  .user-table .ReactTable .rt-tbody .rt-tr-group {
    border: 2px solid #36363659 !important;
    border-radius: 15px !important;
    text-align: center !important;
  }
  .user-table .ReactTable .rt-thead .rt-th,
  .user-table .ReactTable .rt-thead .rt-td {
    text-align: center !important;
    padding-top: 15px;
  }
  .user-table .ReactTable .rt-tbody .rt-td {
    border-right: none !important;
    text-align: center !important;
    padding-top: 20px;
  }
  .hardware-modal {
    width: 420px;
    height: 300px;
  }
  .user-btn-3 {
    margin-right: -20px;
  }
}

@media (min-width: 1024px) and (max-width: 1920px) {
  .user-table .ReactTable {
    width: 800px;
    margin-left: -5px;
  }
  .hardware-modal {
    width: 580px;
    height: 300px;
  }
  .user-btn-3 {
    margin-right: -13px;
  }

  .user-modal {
    margin-left: 170px;
    margin-top: 100px;
  }
}

@media (min-width: 1200px) {
  .user-table .ReactTable {
    width: 700px;
    margin-left: 50px;
  }
  .hardware-modal {
    width: 800px;
    height: 350px;
    margin-left: 60px;
  }
  .user-btn-3 .user-add {
    margin-right: 20px;
  }
  .user-btn-3 .user-export {
    margin-right: 15px;
  }
  .user-modal {
    margin-top: 100px;
    margin-left: 150px;
  }
}

@media (min-width: 1366px) {
  .user-table .ReactTable {
    width: 700px;
    margin-left: 120px;
  }
  .hardware-modal {
    width: 800px;
    height: 350px;
    margin-left: 60px;
  }
  .user-btn-3 {
    margin-right: -20px;
  }

  .user-modal {
    margin-top: 100px;
    margin-left: 150px;
  }
}

@media (min-width: 1920px) {
  .user-table .ReactTable {
    margin-left: 200px;
    width: 1000px;
  }

  .hardware-modal {
    width: 1000px;
    height: 450px;
    margin-left: 60px;
  }
  .user-btn-3 {
    margin-right: 20px;
  }

  .user-modal {
    margin-top: 100px;
    margin-left: 150px;
  }
}
